<template>
  <div class="page" v-show="load">
    <div class="page__content">
      <div class="page__header">
        <breadcrumbs />
        <h1 class="page__title">
          Резидент
        </h1>
      </div>

      <div class="resident-profile">
        <div class="resident__image">
          <img
            :src="resident.avatar.url"
            alt="photo"
            width="400"
            height="300"
          />
          <div class="resident__social">
            <a
              :href="`mailto:${resident.email}`"
              class="resident__social-link mr-10"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="19"
                height="16"
                fill="none"
                viewBox="0 0 19 16"
              >
                <path
                  fill="#209CFE"
                  d="M9.91 7.014l7.286-4.56H2.622l7.287 4.56zm7.286 6.35V4.287L9.91 8.804 2.622 4.287v9.076h14.574zm0-12.742c.483 0 .895.185 1.236.554.37.37.554.796.554 1.278v10.91c0 .482-.185.909-.554 1.278-.341.37-.753.554-1.236.554H2.622c-.483 0-.909-.185-1.278-.554a1.829 1.829 0 01-.511-1.278V2.454c0-.483.17-.909.51-1.278.37-.37.796-.554 1.28-.554h14.573z"
                />
              </svg>
            </a>
            <a :href="resident.vk_profile" class="resident__social-link">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="20"
                height="11"
                fill="none"
                viewBox="0 0 20 11"
              >
                <path
                  fill="#209CFE"
                  d="M9.408 10.954h1.149s.347-.039.524-.23c.163-.176.157-.507.157-.507s-.023-1.546.694-1.773c.705-.225 1.611 1.494 2.571 2.155.726.5 1.278.39 1.278.39l2.568-.035s1.343-.084.706-1.143c-.051-.086-.37-.783-1.909-2.215-1.61-1.499-1.394-1.255.545-3.847 1.182-1.579 1.654-2.543 1.506-2.955-.14-.393-1.009-.289-1.009-.289l-2.89.017s-.213-.03-.373.066C14.77.682 14.67.9 14.67.9s-.458 1.222-1.068 2.26c-1.287 2.193-1.803 2.308-2.013 2.172-.49-.317-.367-1.275-.367-1.955 0-2.124.321-3.01-.626-3.239-.315-.075-.545-.126-1.35-.134-1.03-.011-1.904.003-2.398.245-.33.162-.583.522-.428.543.191.025.624.116.853.43.297.403.286 1.312.286 1.312s.17 2.5-.397 2.811c-.391.214-.926-.222-2.074-2.211C4.501 2.115 4.056.988 4.056.988s-.085-.21-.238-.321c-.185-.137-.444-.18-.444-.18L.626.504S.214.516.063.695c-.135.16-.011.49-.011.49s2.151 5.046 4.586 7.59c2.234 2.332 4.77 2.179 4.77 2.179z"
                />
              </svg>
            </a>
          </div>
        </div>
        <div class="resident__description">
          <div class="resident__description-wrap">
            <div class="resident__name">{{ resident.full_name }}</div>
            <div v-html="resident.about"></div>
          </div>
        </div>
      </div>
      <div class="resident-cards">
        <card-rating
          :categories="resident.resident_categories"
          :rating-place="resident.ratingPlace"
        />
        <card-manager
          :startup-list="resident.startups"
          :startups-count="resident.startupsCount"
        />
        <card-participant
          :event-list="this.events"
          :resident-id="resident.id"
        />
      </div>
    </div>
  </div>
</template>

<script>
import CardRating from "../components/resident/CardRating";
import CardManager from "../components/resident/CardManager";
import CardParticipant from "../components/resident/CardParticipant";
import axios from "axios";

export default {
  name: "ResidentPage",
  components: {
    CardParticipant,
    CardManager,
    CardRating
  },
  data() {
    return {
      resident: {
        avatar: {
          url: ""
        }
      },
      events: [],
      filters: {
        category: [],
        dateRange: null
      },
      load: false
    };
  },
  async mounted() {
    this.$eventBus.$on("filter-list", this.onFilter);
    this.$eventBus.$on("filter-reset", this.filterReset);
    await this.loadData();
    await this.loadEvents();
  },
  beforeDestroy() {
    this.$eventBus.$off("filter-list");
    this.$eventBus.$off("filter-reset");
  },
  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/user/view`);
      url.searchParams.set("id", this.$route.params.id);

      await axios
        .get(url)
        .then(response => {
          this.resident = response.data;
          this.load = true;
          load.hide();
        })
        .catch(error => {
          if (error.response.status === 404) {
            this.$toasted.global.error("Пользователь не найден");
            this.$router.push("/residents");
          } else {
            this.$toasted.global.error(error.response.data.message);
          }
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          load.hide();
        });
    },
    async loadEvents() {
      let load = this.$loading.show();

      let url = new URL(
        `${process.env.VUE_APP_API_URL}/v1/event/list-resident`
      );
      url.searchParams.set("resident_id", this.resident.id);
      // url.searchParams.set("per-page", this.perPage);
      // url.searchParams.set("page", this.currentPage);

      if (this.filters && Array.isArray(this.filters.category)) {
        this.filters.category.forEach(item =>
          url.searchParams.append("categoryIds[]", item)
        );
      }

      if (this.filters.dateRange) {
        url.searchParams.append(
          "begin_time[from]",
          this.filters.dateRange.start
        );
        url.searchParams.append("begin_time[to]", this.filters.dateRange.end);
      }

      await axios
        .get(url)
        .then(response => {
          this.events = response.data._items;
          load.hide();
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          this.$toasted.global.error(error.response.data.message);
          load.hide();
        });
    },
    onFilter(filter) {
      this.currentPage = 1;
      this.filters.category = filter.category;
      this.filters.dateRange = filter.dateRange;
      this.loadEvents();
    },
    filterReset() {
      this.currentPage = 1;
      this.filters.category = [];
      this.filters.dateRange = null;
      this.loadEvents();
    }
  }
};
</script>

