<template>
  <div class="resident-card card-rating">
    <h3 class="resident-card__title">Рейтинг</h3>
    <svg class="card-rating__position" v-if="ratingPlace === 1">
      <use href="@/assets/img/sprite.svg#rating-position-1"></use>
    </svg>
    <svg class="card-rating__position" v-if="ratingPlace === 2">
      <use href="@/assets/img/sprite.svg#rating-position-2"></use>
    </svg>
    <svg class="card-rating__position" v-if="ratingPlace === 3">
      <use href="@/assets/img/sprite.svg#rating-position-3"></use>
    </svg>
    <div
      class="card-rating__category-item"
      v-for="category in categories"
      :key="category.id"
    >
      <div class="card-rating__category">{{ category.title }}</div>
      <div class="card-rating__rating">
        <star-rating
          :read-only="true"
          :star-size="28"
          :rating="category.rating"
        />
        <div class="card-rating__voting">{{ category.vote_count }} голосов</div>
      </div>
    </div>
  </div>
</template>

<script>
import StarRating from "vue-star-rating";

export default {
  name: "CardRating",
  components: {
    StarRating
  },
  props: {
    categories: {
      type: Array,
      default: () => []
    },
    ratingPlace: {
      type: Number,
      default: null
    }
  }
};
</script>

<style scoped></style>
