<template>
  <div class="resident-card card-participant">
    <div class="resident-card__header">
      <h3 class="resident-card__title">Участник</h3>
      <button class="filters__btn-icon" @click="onOpenModal">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="14"
          fill="none"
          viewBox="0 0 16 14"
        >
          <path
            fill="#fff"
            fill-rule="evenodd"
            d="M.962 2.128V3.57c0 .13.047.255.131.353l4.157 4.85a.18.18 0 01.043.117v3.703a1.262 1.262 0 001.75 1.166l2.886-1.203c.471-.196.778-.656.778-1.166v-2.5a.18.18 0 01.043-.117l4.157-4.85a.54.54 0 00.13-.353V2.128c0-.526-.209-1.032-.58-1.404a1.989 1.989 0 00-1.404-.581H2.947c-.526 0-1.031.21-1.404.58a1.988 1.988 0 00-.58 1.405zM13.32 4.113H2.681l3.391 3.956c.196.229.304.52.304.822v3.703a.18.18 0 00.25.167l2.887-1.203a.181.181 0 00.111-.167v-2.5c0-.301.108-.593.304-.822l3.391-3.956zm.636-1.083v-.902a.904.904 0 00-.902-.902H2.947a.904.904 0 00-.902.902v.902h11.91z"
            clip-rule="evenodd"
          />
        </svg>
      </button>
    </div>
    <div class="card-participant__event-count" v-show="eventsCount > 0">
      {{ eventsCount }}
      {{ getNoun(eventsCount, "событие", "события", "событий") }}
    </div>
    <div class="card-participant__event-count" v-show="eventsCount == 0">
      Нет событий
    </div>

    <div class="card-participant__body">
      <div
        class="cart-event"
        v-for="(event, index) in eventList"
        :key="index"
        @click="$router.push(`/event/${event.id}`)"
      >
        <div class="cart-event__img">
          <img :src="event.title_picture.url" alt="" />
          <div class="cart-event__img-wrap">
            <span
              class="cart-event__genre"
              :class="{
                'cart-event__genre--without': !event.categories.length
              }"
              >{{
                event.categories.length ? event.categories[0].title : ""
              }}</span
            >
            <div class="cart-event__img-footer">
              <div class="cart-event__date">
                {{ event.begin_time | formatDate }}
              </div>
              <div class="cart-event__name">
                {{ event.title }}
              </div>
            </div>
          </div>
          <div class="cart-event__img-gradient"></div>
        </div>
        <div class="cart-event__wrap">
          <div
            class="cart-event__description"
            :inner-html.prop="event.body | truncate(7)"
          ></div>
          <div class="cart-event__members">
            <span class="cart-event__members-name">Автор: </span>
            <div class="cart-event__members-tags">
              <span class="cart-event__members-tag">
                {{ event.author.full_name }}
              </span>
            </div>
            <span class="cart-event__members-name" v-show="event.members.length"
              >Участники:
            </span>
            <div class="cart-event__members-tags">
              <span
                class="cart-event__members-tag"
                v-for="(members, index) in event.members"
                :key="index"
              >
                {{ members.full_name }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <modal :show.sync="showModalFilter" :centered="false" :show-close="true">
      <div class="card-participant__filters">
        <filters :filter-type="'residents-event'" />
      </div>
    </modal>
  </div>
</template>

<script>
import mixin from "../../mixin/mixin";
import Modal from "../Modals/Modal";
import Filters from "../Filters";

export default {
  name: "CardParticipant",
  components: { Filters, Modal },
  props: {
    eventList: {
      type: Array,
      default: () => []
    },
    residentId: {
      type: Number
    }
  },
  data() {
    return {
      showModalFilter: false,
      filters: {
        category: [],
        dateRange: null
      }
    };
  },
  mixins: [mixin],
  computed: {
    eventsCount() {
      return this.eventList.length;
    }
  },
  mounted() {
    this.$eventBus.$on("filter-list", () => {
      this.showModalFilter = false;
    });
    this.$eventBus.$on("filter-reset", () => {
      this.showModalFilter = false;
    });
  },
  beforeDestroy() {
    this.$eventBus.$off("filter-list");
    this.$eventBus.$off("filter-reset");
  },
  methods: {
    showMore(e) {
      e.target.classList.toggle("more");
    },
    onOpenModal() {
      this.$eventBus.$emit("open-modal-filters", this.residentId);
      this.showModalFilter = true;
    }
  }
};
</script>

<style scoped></style>
