<template>
  <div class="resident-card card-manager">
    <h3 class="resident-card__title">Руководитель</h3>
    <div class="card-manager__startup" v-show="startupsCount > 0">
      {{ startupsCount }}
      {{ getNoun(startupsCount, "стартап", "стартапа", "стартапов") }}
    </div>
    <div class="card-manager__startup" v-show="startupsCount === 0">
      Нет стартапов
    </div>
    <ul class="card-manager__links">
      <li
        class="card-manager__links-item"
        v-for="startup in startupList"
        :key="startup.id"
      >
        <router-link :to="`/art-startup/${startup.id}`">
          {{ startup.title }}
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import mixin from "../../mixin/mixin";

export default {
  name: "CardManager",
  props: {
    startupList: {
      type: Array,
      default: () => []
    },
    startupsCount: {
      type: Number,
      default: 0
    }
  },
  mixins: [mixin]
};
</script>

<style scoped></style>
